import { HFormat } from "@macashipo/mlib"
import React, { useEffect } from "react"
import DefaultAvatar from "src/assets/images/avatar.jpg"
import styled from "styled-components"

export default function MyUITimeLine(props) {
  const [data, setData] = React.useState(props.data || [])
  const config = {
    ...{ fTitle: "title", fContent: "content", fDate: "date" },
    ...props.config,
  }
  useEffect(() => {
    setData(props.data)
  }, [props.data])
  return (
    <Wrap>
      {data != null &&
        data.length > 0 &&
        data.map((v, i) => {
          console.log("render:", v)
          if (props.renderItem) {
            return props.renderItem(v, i)
          }
          return (
            <li key={i}>
              <div className="a-title">
                {config.fDate && v[config.fDate] && (
                  <div>
                    {HFormat.format(v[config.fDate], {
                      type: "date",
                      format: "YYYY-MM-DD",
                    })}
                  </div>
                )}
                {v[config.fTitle]}
              </div>
              <div className="a-content">{v[config.fContent]}</div>
            </li>
          )
        })}
    </Wrap>
  )
}

const Wrap = styled.ul`
  list-style: none;
  padding: 0;
  & > li {
    list-style: none;
    padding: 0;
    adding-bottom: 1.5rem;
    border-left: 1px solid #abaaed;
    position: relative;
    padding-left: 20px;
    margin-left: 10px;
    &::before {
      content: "";
      width: 15px;
      height: 15px;
      background: white;
      border: 1px solid #4e5ed3;
      box-shadow: 3px 3px 0px #bab5f8;
      box-shadow: 3px 3px 0px #bab5f8;
      border-radius: 50%;
      position: absolute;
      left: -10px;
      top: 0px;
    }
    & > .a-title {
      font-weight: bold;
    }
    & > .a-content {
      color: #4f4f4f;
      font-family: sans-serif;
      line-height: 1.5;
      margin-top: 0.4rem;
      padding-bottom: 1rem;
    }
  }
`
